import { Pipe, PipeTransform } from '@angular/core';
import Data from 'src/Youtube-response/youtube-response';
import { Item } from '../components/search/search-item.model';
import { SortingComponent } from '../components/header/sorting/sorting.component';

@Pipe({
  name: 'filterText'
})
export class FilterTextPipe implements PipeTransform {

  dataItems:Item[] = Data.items;
  // text = SortingComponent.
  public transform(dataItems:Item[], searchText:string):Item[] {
    console.log(searchText)
    if (dataItems.length === 0 || searchText === '') {
      return dataItems;
    }
    return dataItems.filter((item) => (item.snippet.description.trim().toLowerCase()).includes(searchText.trim().toLowerCase()))
  }

}
